.MainR{
    background-color: black;
    padding-top: 5rem;
}
.Reasons{
    padding: 0 2rem;
    display: flex;
    gap: 2rem;

}
.left-r{
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
    grid-auto-rows: 1fr;
}
.left-r>img{
    object-fit: cover;
}
.left-r>:nth-child(1)
{
    width: 12rem;
    grid-row: 1/3;
    height: 28rem;
}
.left-r>:nth-child(2)
{
    width: auto;
    height: 16rem;
    grid-column: 2/4;
}
.left-r>:nth-child(3)
{
    width: 17rem;
    grid-column: 2/3;
    grid-row: 2;
    height: 11rem;
}
.left-r>:nth-child(4)
{
    width: 14rem;
    grid-row: 2;
    grid-column: 3/4;
    height: 11rem;


}
.div{
    display: flex;
}
.divs{
    display: flex;
    font-size: 3.5rem;
    font-weight: bold;
}
.right-r{
    flex: 1 1;
    text-transform: uppercase;
    gap: 1rem;
    display: flex;
    flex-direction: column;
}
.s1{
    color: red;
    font-weight: bold;
    font-size: 1.5rem;
}

.s2{
    color: white;
}
.details-r{
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
    text-transform: uppercase;
    float: left;

}
.div1{
    display: flex;
}
.details-r img{
    width:5%;
}
.partners{
    display: flex;
    border: 5px solid red;
    border-top-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    width: 20rem;
    flex-direction: column;
    float: left;
}
.partnerHeading{
    color: white;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.5rem;
}
.partnerlogo img{
    width:5rem;
}
@media screen and (max-width: 768px) 
{
    .Reasons{
        flex-direction: column;
    }
    .left-r{
        grid-auto-rows: auto;
        overflow: hidden;
    }
    .left-r>:nth-child(1){
        width: 7rem;
        height: 17rem;
    }
    .left-r>:nth-child(2){
        width: 15rem;
        height: 10rem;
    }
    .left-r>:nth-child(3){
        width: 7rem;
        height: 6rem;
    }
    .left-r>:nth-child(4){
        display: none;
    }
    .divs{
        display: flex;
        font-size: 2rem;
        font-weight: bold;
        justify-content: center;
        align-self: center;
    }
    .s1{
        font-weight: bold;
        font-size: large;
    }
    .details-r img{
        width: 1rem;
        height: 1rem;
    }
    .rs1{
        color: white;
        font-size: small;
        font-weight: 200;
    
    }
    .div1{
        display: flex;
    }
    .partners{
        border: 2px solid red;
        width: 15rem;
        flex-direction: column;
        float: left;
    }
    .partnerHeading{
        color: white;
        text-transform: uppercase;
        font-weight: bold;
        font-size: large;
    }
    .MainR{
        margin-bottom: 3rem;
    }
}