.hh1{
    font-size: 3.5rem;
    text-transform: uppercase;
    color: white;
}
.MainC{

    display: flex;
    flex-direction: row;
    gap: 2rem;
}
.C1 h2, .C2 h2, .C3 h2, .C4 h2{
    text-transform: uppercase;
    font-weight: bold;
    font-size: 2rem;
    color: red;
}
.C1{
    margin-left: 5%;
    width: 20%;
}
.C2{
    width: 20%;
}
.C3{
    width: 20%;
}
.C4{
    width: 20%;
}
.C1 ul{
    list-style: none;
}
.l1{
    width: 2rem;
    height:2rem;
}
.li{
    display: flex;
    flex-direction: row;
    gap:1rem;
    padding-top: 1.5rem;
}
.li span{
    font-size: 1.3rem;
    font-weight: bold;
    color: white;
}
.li span a{
    text-decoration: none;
    color: white;
}
#Contact{
    background-color: black;
    padding-bottom: 1rem;
}
.mobile_contact{
    display: none;
}
.p1{
    font-size: 1.5rem;
    color: white;
    padding-bottom: 2rem;
}
.sp{
    color: red;
    font-weight: bold;
}
.hr{
    margin-left: 30%;
    margin-right: 30%;
    border: 2px solid red;
    border-radius: 4px;
}

@media screen and (max-width: 768px) 
{
    .hh1{
        display: none;
        }
    .MainC{
        display: none;
    }
    .mobile_contact{
        display: block;
        margin-left: 2%;
        margin-right: 2%;
        margin-top: 2rem;
    }
    #mobile_contact1{
        font-size: 2rem;
        color: #25D366;
    }    
    #mobile_contact2{
        margin-left: 2rem;
        font-size: 2rem;
        color: #4267B2;
    }
    #mobile_contact3{
        font-size: 2rem;
        margin-left: 2rem;
        color:  #00acee ;
    }    
    #mobile_contact4{
        font-size: 2rem;
        margin-left: 2rem;
        color: #DB4437;
    }       
    .p1{
        font-size: 0.8rem;
        color: white;
    }
    .sp{
        color: red;
        font-weight: bold;
    }

}