
.header{
    display: flex;
    justify-content: space-between;
    background-color: black;
    
}
.logo{
    width: 12rem;
    height: 5rem;
}
.header-list{
    margin-right: 10%;
    list-style: none;
    display: flex;
    gap: 2rem;
    color: white;
}
.header-list li a{
    font-size: 1.2rem;
    text-decoration: none;
    color: white;
}
.hover-underline-animation {
    display: inline-block;
    position: relative;
    color: white;
  }
  
  .hover-underline-animation:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color:red;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }
  
  .hover-underline-animation:hover:after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
.join-btn{
    background-color: transparent;
    border: 2px solid red;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.3rem;
    padding-bottom: 0.3rem;
    font-weight: bolder;
    color: red;
    right: 3rem;
    top: 2rem;

}
.join-btn:hover{
    background-color: red;
    color: black;
    border: 2px solid white;
}
@media screen and (max-width: 768px) 
{
    .header>:nth-child(2)
    {
        position: fixed;
        right: 2rem;
        z-index: 99;
    }
    .header-list{
        flex-direction: column;
        background-color: rgba(0,0,0,0.7);
        padding: 2rem;
        border: 2px solid red;
        border-radius: 2rem;
    }    
    .logo{
        width: 7rem;
        height: 5rem;
    }
}





