@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

.polo{
    font-family: 'Poppins', sans-serif;
    margin: 0;
    padding: 0;
    scroll-padding-top: 2rem;
    scroll-behavior: smooth;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    background-color: white;
}
:root{
    --main-color: #fd4646;
    --text-color: #171427;
    --big-color: #fff;
}
img{
    width: 100%;
}
body{
    color: var(--text-color);
}
.container{
    max-width: 1068px;
    margin: auto;
    width: 100%;
}
section{
    padding: 4rem 0 3rem;
}
header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: var(--big-color);
    box-shadow: 0 1px 4px hsl(0 4% 15% / 10%);
}
.nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 0;
}
.logo{
    font-size: 1.1rem;
    color: var(--text-color);
    font-weight: 400;
    text-decoration: none;
}
#cart-icon{
    font-size: 1.8rem;
    cursor: pointer;
}
.section-title{
    font-size: 1.5rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 1.5rem;
}
.shop{
    margin-top: 2rem;
}

.shop-content{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, auto));
    gap: 1.5rem;
}
.product-box{
    position: relative;
}
.product-box:hover{
    padding: 10px;
    border: 1px solid var(--text-color);
    transition: 0.4s;
}
.product-img{
    width: 100%;
    height: auto;
    margin-bottom: 0.5rem;
}
.product-title{
    font-size: 1.1rem;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 0.5rem;
}
.price{
    font-weight: 500;

}
.add-cart{
    position: absolute;
    bottom: 0;
    right: 0;
    background: var(--text-color);
    color: var(--big-color);
    padding: 10px;
    cursor: pointer;
}
.add-cart:hover{
    background: hsl(249, 32%, 17% );
}
.cart{
    position: fixed;
    top: 0;
    right: 0;
    width: 360px;
    padding: 20px;
    background: var(--big-color);
    box-shadow: -2px 0 4px hsl(0 4% 15% / 10%);
    transition: 0.3s;
    overflow: scroll;
    height: 100%;
    overflow-x: hidden; 
    display: none;
    z-index: 100;
}
.cart::-webkit-scrollbar{
    width: 10px;
    border-radius: 20px;
}
.cart::-webkit-scrollbar-track{
    background-color: #efefef;
    border-radius: 20px;
}
.cart::-webkit-scrollbar-thumb{
    background-image: linear-gradient(#f20bd6,#20bcf2);
    border-radius: 2rem;

}

.cart-title{
    text-align: center;
    font-size: 1.5rem;
    font-weight: 600;
    margin-top: 2rem;

}
.cart-box{
    display: grid;
    grid-template-columns: 32% 50% 18%;
    align-items: center;
    gap: 1rem;
    margin-top: 1rem;
}
.cart-img{
    width: 100px;
    height: 100px;
    object-fit: contain;
    padding: 10px;
}
.detail-box{
    display: grid;
    row-gap: 0.5rem;
}
.cart-product-title{
    font-size: 1rem;
    text-transform: uppercase;
    margin-left: -7rem;
}
.cart-price{
    font-weight: 500;
    margin-left: -9rem;
}
.cart-quantity{
    border: 1px solid var(--text-color);
    outline-color: var(--main-color);
    width: 2.4rem;
    text-align: center;
    font-size: 1rem;
}
.cart-remove{
    font-size: 24px;
    color: var(--main-color);
    cursor: pointer;
}
.total{
    display: flex;
    justify-content: flex-end;
    margin-top: 1.5rem;
    border-top: 1px solid var(--text-color);
}
.total-title{
    font-size: 1rem;
    font-weight: 600;
}
.total-price{
    margin-left: 0.5rem;
}
.btn-buy{
    display: flex;
    margin: 1.5rem auto 0 auto;
    padding: 12px 20px;
    border: none;
    background: red;
    color: var(--big-color);
    font-size: 1rem;
    font-weight: 500;
    cursor: pointer;
    margin-bottom: 2rem;
}
.btn-buy:hover{
    background: var(--text-color);
}
#close-cart{
    position: absolute;
    top: 1rem;
    right: 0.8rem;
    font-size: 2rem;
    color: var(--text-color);
    cursor: pointer;
}
#back-cart{
    position: absolute;
    top: 1rem;
    left: 0.8rem;
    font-size: 2rem;
    color: var(--text-color);
    cursor: pointer;
}

#p1,#p2,#p3,#p4,#p5,#p6,#p7,#p8{
    display: none;
}
.filter-btn{
    padding: 1rem 2rem;
    font-size: 1.5rem;
    border: 2px solid white;
    text-align: center;
    background-color: var(--big-color);
    cursor: pointer;
    margin-bottom: 1rem;
    margin-left: 1rem;
    text-decoration: underline;
    text-decoration-color: white;
}
.filter-btn:hover {
    padding: 10px;
    border: 2px solid var(--text-color);
    border-radius: 6px;
    transition: 0.4s;
}

.in1{
    width: 90%;
    height: 2rem;
    margin-top: 1rem;
    font-size: 1rem;
}
.personal_info{
    display: flex;
    flex-direction: column;
}
.Maincheckform{
    display: none;
}


@media (max-width:1080px){
    .nav{
        padding: 15px;
    }
    .section{
        padding: 3rem 0 2rem;
    }
    .container{
        margin: 0 auto;
        width: 90%;
    }
    .shop {
        margin-top: 2rem !important;
    }
}
@media (max-width:400px){
    .nav{
        padding: 15px;
    }
    .logo{
        font-size: 1rem;
    }
    .cart{
        width: 320px;
    }
}
@media (max-width:360px){
    .shop {
        margin-top: 1rem !important;
    }
    .cart{
        width: 280px;
    }
}
@media screen and (max-width: 768px) 
{
    .polo{
        margin-left: 0;
        margin-right: 0;
    }
    section{
        padding-left: 0;
    }
    .section-title{
        margin-top: -1rem;
    }
    .filter{
        padding: 0;
    }
    .filter-btn{
        padding: 0.2rem 0.2rem;
        font-size: small;
        text-align: center;
        transition: none;
    }
    .product-title{
    font-size: medium;
    }
    .add-cart{
        padding: 8px;
    }
    .shop-content{
        display: flex;
        flex-direction: column;
    }
    .personal_info{
        margin-left: 2rem;
    }
    #back-cart{
        margin-left: 1rem;
    }
    .btn-buy{
        margin-left: 1.3rem;
    }
}