
#slider{
    overflow: hidden;
    margin-left:22%;
    margin-right: 27%;
}
#slider figure{
    position: relative;
    width: 500%;
    display: flex;
    margin: 0;
    left: 0;
    animation: 5s slider infinite;
}

@keyframes slider{
    0%{
        left: 0;
    }
    20%{
        left: 0;
    }
    25%{
        left: -100%;
    }
    45%{
        left: -100%;
    }
    50%{
        left: -200%;
    }
    70%{
        left: -200%;
    }
    75%{
        left: -300%;
    }
    95%{
        left: -300%;
    }
    100%{
        left: -400%;
    }
    

    
    

}

.mainr{
    background-color: black;
    padding-bottom: 3rem;
    margin-top: 2rem;
}
.card{
    width: 800px;
    min-height: 100%;
    background: black;
    border-radius: 20px;
    box-shadow:  0 10px 20px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin: 0 10px;
    border: 4px solid red;
	padding-left:1rem;
	padding-right:1rem;
}

.client-img{
    width: 15vh;
    height: 18vh;
    object-fit: cover;
    border-radius: 40%;
    border: 3px solid red;
}
.client-name{
    font-size: 1rem;
    text-transform: uppercase;
    background-color: red;
    font-weight: bold;
    color: black;
    padding-left: 0.5rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    border-bottom-right-radius: 1rem;
    border-top-right-radius: 1rem;
}
.card-body{
    width: 120%;
    min-height: 100%;
    height: auto;
    margin-left: 20px;
    position: relative;
    padding-bottom: 50px;
}
.review{
    font-size: 20px;
    line-height: 30px;
    margin-top: 30px;
    color: white;
}
.rating{
    position: absolute;
    bottom: 30px;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
.rating img{
	width:2rem;
}



@media(max-width: 800px){
    .container{
        width: 50%;
        margin: auto;
    }
    .card{
        flex-direction: column;
    }
    .card-thumb{
        width: 100%;
        border-radius: 10px;
    }
    .card-body{
        width: 100%;
        min-height: auto;
        margin-left: 0;
    }
}
h1{
	color: white;
    font-size: 4rem;
    font-weight: bold;
    text-transform: uppercase;
}
@media screen and (max-width: 768px) 
{
    .h1_review{
        font-size: xx-large;
    }        
    #slider{
        margin-left:5%;
        margin-right: 5%;
    }
    .client-img{
        width: 15vh;
        height: 18vh;
        object-fit: cover;
    }
    .card{
        margin: 0 10px;
        padding-left:0.2rem;
        padding-bottom: 0;
    }
    .client-name{
        font-size: medium;
        font-weight: bold;
    }
   
    .review{
        font-size: small;
        line-height: 1rem;
        margin-top: 30px;
        color: white;
        text-align: left;
    }
    .rating img{
        width:1.5rem;
    }
    .rating{
        bottom: 30px;
    }
  
    
    
}